export default "import SearchIcon from 'component/Icon/SearchIcon';\nimport Input from 'component/Input';\nimport { useState } from 'react';\nimport styles from './index.less'\n\nconst InputBaseView = () => {\n    const [searchText, setSearchText] = useState('1346789');\n    const [limitVal, setLimitVal] = useState('');\n    return (\n        <div className={styles.container}>\n            <div className={styles.item}>\n                <span>最基础的输入：</span>\n                <Input></Input>\n            </div>\n            <div className={styles.item}>\n                <span>带前缀图标和清空按钮：\n                </span>\n                <Input\n                    allowClear\n                    value={searchText}\n                    prefix={<SearchIcon />}\n                    onChange={(e) => setSearchText(e.target.value)}\n\n                ></Input>\n            </div>\n            <div className={styles.item}>\n                <span>   有长度限制的输入：\n                </span>\n                <Input\n                    value={limitVal}\n                    maxLength={10}\n                    onChange={(e) => setLimitVal(e.target.value)}\n\n                ></Input>\n            </div>\n            <div className={styles.item}>\n                <span>   禁用状态：\n                </span>\n                <Input\n                    value={'disable'}\n                    disabled\n                ></Input>\n            </div>\n\n\n            <div className={styles.item}>\n                <span>线型输入框：\n                </span>\n                <Input\n                    allowClear\n                    value={searchText}\n                    prefix={<SearchIcon />}\n                    onChange={(e) => setSearchText(e.target.value)}\n                    type=\"line\"\n                ></Input>\n            </div>\n            <div className={styles.item}>\n                <span>出错的输入框：\n                </span>\n                <Input\n                    allowClear\n                    value={searchText}\n                    prefix={<SearchIcon />}\n                    onChange={(e) => setSearchText(e.target.value)}\n                    error=\"一个错误提示\"\n                ></Input>\n            </div>\n            \n            <div className={styles.item}>\n                <span>密码输入带密码可见切换：\n\n                </span>\n                <Input\n                    value={'27674838349394'}\n                    password\n                ></Input>\n            </div>\n\n        </div>\n    )\n}\n\nexport default InputBaseView;";