import classNames from 'classnames';
import ClearIcon from 'component/Icon/ClearIcon';
import { useEffect, useRef, useState } from 'react';
import styles from './index.less'

interface Props {
    maxLength?: number;
    value?: string;
    allowClear?: boolean;
    onChange?: (e: any) => void;
    placeholder?: string;
    disabled?: boolean;
    isAutoLine?: boolean;
    minRow?: number;
    maxRow?: number;
}

const TextArea = (props: Props) => {
    const {
        maxLength, value,
        allowClear, onChange,
        placeholder, disabled,
        isAutoLine, minRow
    } = props;
    const [inputHeight, setInputHeight] = useState(0);
    const inputRef = useRef<HTMLTextAreaElement>(null);
    useEffect(() => {
        if (inputRef.current) {
            setInputHeight(inputRef.current.offsetHeight)
        }
    }, [])

    const handleChange = (e:any) => {
        if (onChange) {
            onChange(e)
        }
    }

    return (
        <div className={classNames(styles.container, {
            [styles.disabled]: disabled,
            [styles.autoHeight]: isAutoLine,
            [styles.normal]: !isAutoLine,
        }
        )}>
            <textarea
                spellCheck={false}
                className={styles.textarea}
                placeholder={placeholder}
                maxLength={maxLength}
                ref={inputRef}
                value={value}
                onChange={handleChange}
                disabled={disabled}
                rows={minRow}
            ></textarea>
            {
                value && allowClear ? (
                    <span onClick={(e) => {
                        if (onChange) {
                            onChange({ target: { value: '' } });
                            e.stopPropagation();
                        }
                    }}
                        className={styles.clearIcon}
                        style={{ top: inputHeight - 18 }}
                    >
                        <ClearIcon />
                    </span>

                ) : null
            }
            {
                maxLength ? <div className={styles.maxTip}>{value?.length}/{maxLength}</div> : null
            }

        </div>
    )
}

export default TextArea;