export default "import styles from './index.less'\nimport TextArea from 'component/TextArea';\nimport { useState } from 'react';\nconst TextAreaView = () => {\n\n    const [val, setVal] = useState('');\n    const [limit, setLimit] = useState('limit');\n\n    return (\n        <div className={styles.container}>\n            <div className={styles.item}>\n                <span>基本的文本域：</span>\n                <TextArea\n                    placeholder=\"请输入\"\n                    value={val}\n                    onChange={(e) => setVal(e.target.value)}\n                    allowClear\n                />\n            </div>\n\n            <div className={styles.item}>\n                <span>禁用的文本域：</span>\n                <TextArea\n                    placeholder=\"请输入\"\n                    disabled\n                />\n            </div>\n\n            \n            <div className={styles.item}>\n                <span>有附加区域的文本域：</span>\n                <TextArea\n                    placeholder=\"请输入\"\n                    maxLength={30}\n                    value={limit}\n                    onChange={(e)=> setLimit(e.target.value)}\n                    allowClear\n                />\n            </div>\n            {/* <div>\n                <TextArea\n                    placeholder=\"请输入\"\n                    value={limit}\n                    onChange={(e)=> setLimit(e.target.value)}\n                    allowClear\n                    isAutoLine\n                    minRow={1}\n                    maxRow={6}\n                />\n            </div> */}\n           \n\n        </div>\n    )\n}\n\nexport default TextAreaView;";